@media screen and (min-width: 1280px) {
  * {
    background-color: #f8f8f8;
    color: #1b1a20;
    font-family: "Montserrat", monospace;
    margin: 20px;
    overflow-x: hidden;
  }

  .phone-only-display {
    display: none;
  }

  .intro {
    display: flex;
    align-items: center;
    justify-content: space-between;
    overflow-y: hidden;
  }

  .hello {
    font-size: 160px;
  }

  .scroll-down {
    display: flex;
    transform: rotate(90deg);
    align-self: flex-end;
    margin-bottom: 50px;
  }

  .scroll-all {
    display: flex;
    transform: rotate(90deg);
    margin-bottom: 50px;
    position: fixed;
    left: -80px;
  }

  .scroll-content {
    margin-right: 0px;
  }

  .scroll-arrow {
    animation: float 1.2s ease-out infinite;
  }

  @keyframes float {
    50% {
      transform: translate(10px, 0px);
    }
  }

  .heading {
    padding: auto 10px;
    font-size: 50px;
  }

  .content {
    padding: auto 5px;
    font-size: 21px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }

  .education-flex {
    display: flex;
  }

  .dayananda-sagar-logo {
    height: 40px;
    width: 290px;
    margin: auto 40px;
  }

  .christ-college-logo {
    height: 86px;
    width: 146px;
  }

  .national-college-logo {
    height: 84px;
    width: 128px;
  }

  .dream-world-school-logo {
    height: 75px;
    text-align: center;
    margin: auto 76px;
  }

  .project-1 {
    background-image: url("./images/Income-Tax_Calculator_screenshot.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: -10px -850px;
    min-height: 100px;
    background-attachment: fixed;
  }

  .project-2 {
    background-image: url("./images/21Clones.svg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 15px -100px;
    min-height: 100px;
    background-attachment: fixed;
  }

  .project {
    margin: 0px 20px;
    height: 640px;
  }

  .transperent-bg {
    background: #000;
    opacity: 0.75;
    margin: 0px;
    width: 41.6%;
    height: 87.5%;
    padding-left: 100px;
    padding-right: 0px;
    padding-top: 80px;
  }

  .project-title {
    width: 300px;
    font-size: 35px;
    font-weight: bold;
    color: #fff;
    background: #000;
    padding-top: 100px;
  }

  .project-content {
    width: 300px;
    font-size: 21px;
    color: #fff;
    background: #000;
    margin-top: 50px;
  }

  .skills {
    display: flex;
    margin: 10px;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
  }

  .skill-icons {
    width: 135px;
    align-self: center;
  }

  .heroku {
    height: 135px;
  }

  .contact {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: flex-start;
  }

  .contact-top {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-self: flex-start;
  }

  .social-icons {
    margin: 20px;
  }

  .icon-links-top,
  .icon-links-bottom {
    height: 72px;
    margin: 0px;
    padding: 0px;
    overflow: hidden;
  }
} 

/* Media Queries for Devices with width less than 814px */

@media screen and (max-width: 814px) {
