    color: #1b1a20;
    font-family: "Montserrat", monospace;
    margin: 10px;
    overflow-x: hidden;
  }

  .phone-only-display {
    display: block;
    position: absolute;
    top: 300px;
    background: transparent;
  }

  .hello-on-phone {
    font-size: 45px;
  }

  .intro {
    display: flex;
    align-items: center;
    justify-content: space-between;
