    margin: 0px 20px;
    height: 640px;
  }

  .transperent-bg {
    background: #000;
    opacity: 0.75;
    margin: 0px;
    width: 21.6%;
    height: 87.5%;
    padding-left: 10px;
    padding-right: 90px;
    padding-top: 80px;
  }

  .project-title {
    width: 120px;
    font-size: 21px;
    font-weight: bold;
    color: #fff;
    background: #000;
    padding-top: 75px;
  }

  .project-content {
    width: 140px;
