  }

  .social-icons {
    margin: 20px;
  }

  .icon-links-bottom {
    height: 60px;
    overflow: hidden;
    width: 60px;
    margin: 10px 20px;
    padding: 0px;
  }

  .skills {
    display: flex;
    margin: 10px;
    flex-wrap: wrap;
    align-content: center;
