  }

  .skill-icons {
    width: 40px;
    align-self: center;
  }

  .heroku {
    height: 35px;
  }
}

/* Media Queries for Devices with width less than 1280px */

@media screen and (min-width: 814px) and (max-width: 1280px) {
  .skill-icons {
    width: 50px;
    align-self: center;
  }

  .heroku {
    height: 50px;
  }

}